
import { defineComponent } from 'vue'

export default defineComponent({
    props: {
        data: {
            type: Array,
            required: true
        } as any,
        showKey: {
            type: Boolean,
            default: true
        },
        value: {
            type: String || Object,
            required: false
        },
        isDisabled: {
            type: Boolean,
            default: false,
            required: false
        },
        classSelectType: {
            type: String,
            required: false
        },
        validateClass: {
            type: String,
            required: false
        },
        filterListOut: {
            type: Object || Array,
            required: false,
            default: null,
        },
        disabledOptionData: {
            type: Object || Array,
            required: false,
            default: null,
        },
        showOptionAll: {
            type: Boolean,
            default: false,
            required: false
        }
    },
    data() {

        return {
            selectedValue: '',
            setDefault: '',
            commonData: [] as any,
            filterData: [] as any
        }
    },
    mounted() {
        this.filterList()
        this.isOptionDisabled;
        let setDefault: any
        try {
            if (this.value) {
                const commonCode = this.$store.state.CommonCodeModule.commonCode;
                if (commonCode) {
                    let existingKey: any;
                    existingKey = this.data.find((option: any) => {
                        if (option.key == this.value) {
                            return option;
                        }
                    });
                    if (!existingKey) {
                        let allKey: any = document.querySelector('#AllList');
                        if (allKey) {
                            setDefault = allKey.value;
                            this.updateValue(setDefault);
                        }
                    } else {
                        this.updateValue(this.value);
                    }
                }
            }
        } catch (error) {
            console.log(error);
        }

    },
    methods: {
        updateValue(newValue?: any) {
            try {
                this.selectedValue = newValue;
                this.$emit('update:modelValue', this.selectedValue);
            } catch (error) {
                console.log(error);
            }
        },
        filterList() {
            if (this.filterListOut) {
                this.filterData = this.filterListOut as any
                this.commonData = this.data.filter((item) => !this.filterData.some((list) => item.key === list.key));
                return this.commonData
            } else {
                this.commonData = this.data
                return this.commonData
            }
        },
        isOptionDisabled(option) {
            if (this.disabledOptionData) {
                return this.disabledOptionData.some((item) => item.key === option.key);
            } else {
                return false;
            }
        }

    },
    computed: {
        // setDefaults(): any {
        //     let setDefault: any
        //     try {
        //         if (this.value || this.$route.query.type == 'register') {
        //             const commonCode = this.$store.state.CommonCodeModule.commonCode;
        //             if (commonCode) {
        //                 let existingKey: any;
        //                 existingKey = this.data.find((option: any) => {
        //                     if (option.key == this.value) {
        //                         return option;
        //                     }
        //                 });
        //                 if (existingKey) {
        //                     setDefault = this.value
        //                 } else {
        //                     setDefault = this.data[0].key
        //                     this.updateValue(setDefault);
        //                 }
        //             }
        //         }
        //     } catch (error) {
        //         console.log(error);
        //     }
        //     return setDefault;
        // },
    },
    watch: {
        value(newValue: any): any {
            this.selectedValue = newValue;
        },
        watchPlaceholder() {
            if (this.value || this.showOptionAll || this.data != undefined || this.selectedValue != '') {
                return true;
            } else {
                return false
            }
        }

    }
})
